.card-container {
  display: flex;
  flex-direction: column;
  background-image: url("https://cdn.pixabay.com/photo/2015/06/04/07/07/pink-797181_1280.jpg");
  border: 1px solid #cccccc;
  border-radius: 5px;
  padding: 0 0 20px 0;
  cursor: pointer;
  font-size: 30px;
  margin: 20px;

  -moz-osx-font-smoothing: grayscale;
  backface-visibility: hidden;
  transform: translateZ(0);
  transition: transform 0.25s ease-out;
}
.card-container:hover {
  transform: scale(1.02) rotate(3deg);
  background-image: url("https://cdn.pixabay.com/photo/2014/01/13/13/08/herbsthintergund-243388_1280.jpg");
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);

  font-size: 40px;
}

h2,
p {
  font-family: "Amatic SC", cursive;
  margin: 2px;
}

@media screen and (max-width: 800px) {
  .column {
    flex: 50%;
    max-width: 50%;
    padding: 0;
  }
}
@media screen and (max-width: 600px) {
  .column {
    flex: 100%;
    max-width: 100%;
    padding: 0;
  }
}
