.App {
  background-image: url("https://cdn.pixabay.com/photo/2019/08/31/13/40/space-4443474_1280.jpg");
  /* background-image: url("https://cdn.pixabay.com/photo/2019/08/17/18/25/forest-4412721_1280.jpg"); */
  min-height: 500px;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  justify-content: center;
  display: inherit;
  margin: 0;
  top: 0;
  box-sizing: border-box;
  background-color: #fc9b62;
  background: -webkit-radial-gradient(top, #fff2c5, #ffb057);
  background: -moz-radial-gradient(top, #fff2c5, #ffb057);
  background: radial-gradient(to bottom, #fff2c5, #ffb057);
}
