h3 {
  position: relative;
  font-size: 20px;
  font-family: "Raleway", cursive;
}

.footer-container {
  margin-top: 30px;
  background-color: rgba(37, 37, 37, 0.93);
  width: 100%;
  padding: 50px 0 50px 0;
  color: rgba(238, 238, 238, 0.763);
}

@media screen and (max-width: 800px) {
  h3 {
    position: relative;
    font-size: 15px;
  }
  .footer-container {
    background-color: rgba(37, 37, 37, 0.842);
    width: 100%;
    padding: 30px 0 30px 0;
  }
}
