.search-box {
  -webkit-appearance: none;
  border: 3px solid rgba(37, 37, 37);
  background-color: rgb(241, 239, 239);
  border-radius: 8px;
  outline: none;
  padding: 8px;
  width: 33rem;
  line-height: 3rem;
  margin-bottom: 1rem;
  font-size: 33px;
  font-family: "Amatic SC", cursive;
  box-shadow: -1px -1px 0px rgb(236, 170, 70), 0px -1px 0px rgb(236, 170, 70),
    1px -1px 0px rgb(236, 170, 70), -1px 0px 0px rgb(236, 170, 70),
    1px 0px 0px rgb(236, 170, 70), -1px 1px 0px rgb(236, 170, 70),
    0px 1px 0px rgb(236, 170, 70), 1px 1px 0px rgb(236, 170, 70);
}

@media screen and (max-width: 800px) {
  .search-box {
    border: 3px solid rgba(37, 37, 37);
    background-color: rgb(241, 239, 239);
    border-radius: 8px;
    outline: none;
    padding: 8px;
    width: 18rem;
    line-height: 2.5rem;
    margin-bottom: 0.5rem;
    font-size: 25px;
    font-family: "Amatic SC", cursive;
  }
}
